@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
html{line-height:1.15;-webkit-text-size-adjust:100%}main{display:block}h1,h2,h3{font-size:2em;margin:0.67em 0}img{border-style:none}button{font-family:inherit;font-size:100%;line-height:1.15;margin:0;overflow:visible;text-transform:none;background-color:transparent;color:inherit;border-width:0;padding:0;cursor:pointer}button,[type="button"],[type="submit"]{-webkit-appearance:button}button::-moz-focus-inner,[type="button"]::-moz-focus-inner,[type="submit"]::-moz-focus-inner{border-style:none;padding:0}button:-moz-focusring,[type="button"]:-moz-focusring,[type="submit"]:-moz-focusring{outline:1px dotted ButtonText}*,*::before,*::after{box-sizing:border-box}a{background-color:transparent;text-decoration:none;color:inherit;cursor:pointer}input::-moz-focus-inner{border:0;padding:0;margin:0}h1,h2,h3{margin:0;font-size:inherit;font-weight:inherit}p{margin:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.app{font-size:1em;font-family:'Roboto', sans-serif;color:#222831;background-color:#eeeeee}.main{margin:0 auto;max-width:1024px;padding:0 1.3em}a:hover{color:#00adb5}


.header{text-align:right;position:fixed;top:0;background-color:#eeeeee;width:100%;border-bottom:3px solid transparent;border-image:linear-gradient(to bottom right, #00adb5 50%, #0012b5 75%, #a000b5 100%);border-image-slice:1}.header a{padding:0 1em;font-family:'Montserrat', sans-serif}.header_menu{margin:0.8em}.header_menu a:not(:last-of-type){border-right:solid 1px #7b9b9c}

.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;padding-top:65px}.card_intro{margin:0 1em}.intro_name{text-align:center;font-size:1.7em}.intro_title{text-align:center;font-size:1.1em;margin:1em 0}.contacts_incons{text-align:center;font-size:1.6em}.contacts_incons a{margin:1em}.contacts_location i{color:#00adb5}.intro_contacts{margin:0 auto;max-width:300px}.intro_contacts>*{line-height:1.6em}.card_presentation{margin:1em}

